import baseModal from "../base/baseModal";

export default {
  data() {
    return {};
  },

  components: {
    baseModal
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    cancelText: {
      type: String,
      default: '取消'
    },
    confirmText: {
      type: String,
      default: '确定'
    },
    backdrop: {
      type: Boolean,
      default: true
    },
    animated: {
      type: Boolean,
      default: true
    },
    modalSize: {
      type: String,
      default: "md"
    }
  },
  mounted: function () {
    //获得baseModal节点
    if (!this.selectComponent) {
      throw new Error("小程序sdk暂不支持节点操作selectComponent");
    }

	this.baseModal = this.$refs.baseModalRef;
  },
  methods: {
    show: function () {
      this.baseModal.showModal();
    },
    hide: function () {
      this.baseModal.hideModal();
    },
    //cancel
    cancelModalFun: function () {
      this.hide();
      this.$emit("cancelEvent");
    },
    //success
    confirmModalFun: function () {
      this.$emit("confirmEvent");
    }
  }
};
